import { DateTime } from "luxon";

const getFormattedValue = (value = 0, digits = 2) =>
  parseFloat(value)
    .toFixed(digits)
    .replace(/\.0+$/, "");

const formattedDate = date => DateTime.fromISO(date).toFormat("D");

